<template>
  <v-data-table :headers="headers" :items="compras" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Ingresos</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="100%">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Nuevo
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="4" sm="4" md="4">
                    <v-autocomplete
                      ref="rol"
                      v-model="editedItem.tipoComprobante"
                      :items="tipoComprobante"
                      label="Tipo Comprobante"
                      placeholder="Tipo Comprobante"
                      required
                    ></v-autocomplete>
                  </v-col>
                  <v-col cols="4" sm="4" md="4">
                    <v-text-field
                      v-model="editedItem.serieComprobante"
                      label="Serie Comprobante"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="4" sm="4" md="4">
                    <v-text-field
                      v-model="editedItem.numeroComprobante"
                      label="Nmero Comprobante"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="8" sm="8" md="8">
                    <v-autocomplete
                      ref="rol"
                      v-model="editedItem.persona"
                      :items="clientes"
                      item-text="nombre"
                      item-value="_id"
                      label="Clientes"
                      placeholder="Clientes"
                      required
                    ></v-autocomplete>
                  </v-col>
                  <v-col cols="4" sm="4" md="4">
                    <v-text-field
                      v-model="editedItem.impuesto"
                      label="Impuesto"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="12" md="12">
                    <v-autocomplete
                      ref="rol"
                      v-model="editedItem.articulo"
                      :items="articulos"
                      item-text="nombre"
                      item-value="_id"
                      label="Articulos"
                      placeholder="Articulos"
                      required
                    ></v-autocomplete>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                Cancel
              </v-btn>
              <v-btn color="blue darken-1" text @click="save">
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title v-if="editedItem.estado == 1" class="headline"
              >Desactivar compra?</v-card-title
            >
            <v-card-title v-if="editedItem.estado == 0" class="headline"
              >Activar compra?</v-card-title
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancel</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >OK</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        color="red"
        v-if="item.estado == 1"
        @click="deleteItem(item)"
      >
        mdi-minus-circle
      </v-icon>
      <v-icon
        small
        color="brown"
        v-if="item.estado == 0"
        @click="deleteItem(item)"
      >
        mdi-minus-circle-off
      </v-icon>
    </template>
    <template v-slot:item.estado="{ item }">
      <span class="blue--text" v-if="item.estado == 1">
        Activo
      </span>
      <span class="red--text" v-else>
        Inactivo
      </span>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="listarCompras">
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<script>
import axios from 'axios';

export default {
  data: () => ({
    compras: [],
    articulos: [],
    clientes: [],
    tipoComprobante: ['NOTA DEBITO', 'FACTURA', 'NOTA CREDITO'],
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Usuario', value: 'usuario.nombre' },
      { text: 'Proveedor', value: 'persona.nombre' },
      { text: 'Tipo Comprobante', value: 'tipoComprobante' },
      { text: 'Serie Comprobante', value: 'serieComprobante' },
      { text: 'Numero Comprobante', value: 'numeroComprobante' },
      { text: 'Fecha', value: 'createdAt' },
      { text: 'Impuesto', value: 'impuesto' },
      { text: 'Total', value: 'total' },
      { text: 'Estado', value: 'estado' },
    ],
    editedIndex: -1,
    editedItem: {
      tipoComprobante: '',
      serieComprobante: '',
      numeroComprobante: '',
      proveedor: '',
      impuesto: '',
      detalles: [{
        _id: '',
        articulo: '',
        cantidad: '',
        precio: '',
        descuento: '',
      }],
      total: '',
    },
    defaultItem: {
      tipoComprobante: '',
      serieComprobante: '',
      numeroComprobante: '',
      proveedor: '',
      impuesto: '',
      detalles: [{
        _id: '',
        articulo: '',
        cantidad: '',
        precio: '',
        descuento: '',
      }],
      total: '',
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Nuevo' : 'Editar';
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.listarCompras();
    this.listarArticulos();
    this.listarClientes();
  },

  methods: {
    listarCompras() {
      const { token } = this.$store.state;
      axios
        .get('/compra', {
          headers: { token },
        })
        .then((response) => {
          console.log(response.data);
          this.compras = response.data.compra;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    listarArticulos() {
      const { token } = this.$store.state;
      axios
        .get('/articulo', {
          headers: { token },
        })
        .then((response) => {
          this.articulos = response.data.articulo;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    listarClientes() {
      const { token } = this.$store.state;
      axios
        .get('/persona/listClientes', {
          headers: { token },
        })
        .then((response) => {
          this.clientes = response.data.persona;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    save() {
      if (this.editedIndex > -1) {
        // Put
        const { token } = this.$store.state;
        axios
          .put(`/compra/${this.editedItem._id}`, this.editedItem, {
            headers: { token },
          })
          .then((response) => {
            console.log(response.data);
            this.listarCompras();
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        // Post
        const { token } = this.$store.state;
        axios
          .post('/compra', this.editedItem, {
            headers: { token },
          })
          .then((response) => {
            console.log(response.data);
            this.listarCompras();
          })
          .catch((error) => {
            console.log(error);
          });
      }
      this.close();
    },
    editItem(item) {
      this.editedIndex = this.compras.indexOf(item);
      this.editedItem = { ...item };
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.compras.indexOf(item);
      this.editedItem = { ...item };
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      if (this.editedItem.estado == 0) {
        this.activateItem();
      } else this.deactivateItem();
      this.closeDelete();
    },

    activateItem() {
      const { token } = this.$store.state;
      axios
        .put(`/compra/activar/${this.editedItem._id}`, this.editedItem, {
          headers: { token },
        })
        .then((response) => {
          console.log(response.data);
          this.listarCompras();
        })
        .catch((error) => {
          console.log(error);
        });
    },

    deactivateItem() {
      const { token } = this.$store.state;
      axios
        .put(`/compra/desactivar/${this.editedItem._id}`, this.editedItem, {
          headers: { token },
        })
        .then((response) => {
          console.log(response.data);
          this.listarCompras();
        })
        .catch((error) => {
          console.log(error);
        });
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },
  },
};
</script>
